import React, { Component } from 'react'
import { CLOUDPATH, CLOUDPATH_TAGGSHOP, HOST_URL } from '../../../actions/api';
import { Link } from 'react-router-dom';
import { logoutUser } from '../../../actions/loginActions';
import { error } from 'jquery';

export default class userSiderBar extends Component {
    logOutAccount=(e)=>{  
        //   localStorage.removeItem("token") 
        //   localStorage.removeItem("loginOwnerId") 
        //   localStorage.removeItem("ownerId") 
        //   localStorage.removeItem("wallId") 
        logoutUser().then(res=> {
            localStorage.clear()
            window.location.replace(`${HOST_URL}`)
        }).catch(error=> {
            console.error("catch----------------------error",error)
            
        })
      }

      closeSidebar = () => {
        const elem = document.getElementById('kt_quick_user_close')
        if(elem) elem.click()
      }

    render() {
        const {authorized} = this.props  
        const user = authorized?.userData?.user_data.user 
        const profileImg = user?.imgSource ? `${CLOUDPATH}${user?.picture}` :user?.picture
        const user_name = user?.name

        return (
            <div id="kt_quick_user" className="offcanvas offcanvas-end p-10" tabIndex="-1" aria-labelledby="offcanvasRightLabel" aria-modal="true" role="dialog" style={{ visibility: 'visible' }}>
                 
                <div className="offcanvas-header d-flex align-items-center justify-content-between pb-5">
                    <h3 className="font-weight-bold m-0"> Profile </h3>
                    <button type="button" className="btn btn-xs btn-icon btn-light btn-hover-primary" id="kt_quick_user_close" data-bs-dismiss="offcanvas" aria-label="Close">
                        <i className="ki ki-close icon-xs text-muted"></i>
                    </button>
                </div>

                <div className="offcanvas-content pr-5 mr-n5">

                    <div className="d-flex align-items-start mt-5">
                        <div className="symbol symbol-100 mr-5 symbol-light-warning mt-2">
                        {profileImg ? <div className="symbol-label" style={{ backgroundImage: `url('${profileImg}')` }}></div>
                            :
                            <span className="symbol-label font-size-h1 font-weight-bold text-uppercase">{user_name ? user_name[0]?.toUpperCase() : ""}</span>}
                            <span className="premium-user-sign user-sign-dropdown-sign">
                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="20" height="20" viewBox="0 0 20 20">
                                    <defs>
                                        <linearGradient id="linear-gradient" x1="0.5" y1="1.077" x2="0.5" gradientUnits="objectBoundingBox">
                                            <stop offset="0" stopColor="#fd8800"></stop>
                                            <stop offset="1" stopColor="#fec400"></stop>
                                        </linearGradient>
                                        <linearGradient id="linear-gradient-2" x1="0.5" y1="1" x2="0.5" gradientUnits="objectBoundingBox">
                                            <stop offset="0" stopColor="#ffe59a"></stop>
                                            <stop offset="1" stopColor="#ffffd5"></stop>
                                        </linearGradient>
                                    </defs>
                                    <g id="Group_4832" data-name="Group 4832" transform="translate(6313.593 292.593)">
                                        <g id="Group_4635" data-name="Group 4635" transform="translate(-6313.593 -292.593)">
                                            <g id="Group_4634" data-name="Group 4634" transform="translate(0 0)">
                                                <circle id="Ellipse_183" data-name="Ellipse 183" cx="10" cy="10" r="10" fill="url(#linear-gradient)"></circle>
                                            </g>
                                        </g>
                                        <g id="star" transform="translate(-6310.039 -289.039)">
                                            <path id="Path_4779" data-name="Path 4779" d="M7.018.15,8.581,3.631l3.8.391a.634.634,0,0,1,.355,1.1L9.9,7.716l.781,3.73a.62.62,0,0,1-.924.675L6.414,10.2l-3.3,1.918a.62.62,0,0,1-.924-.675l.781-3.73L.127,5.123a.634.634,0,0,1,.355-1.1l3.8-.391L5.846.15A.63.63,0,0,1,7.018.15Z" transform="translate(0.089 0.25)" fillRule="evenodd" fill="url(#linear-gradient-2)"></path>
                                        </g>
                                    </g>
                                </svg>
                            </span>
                        </div>
                        <div className="d-flex flex-column w-100">
                            <a href="profile" className="font-weight-bold font-size-h6 text-dark-75 text-hover-primary text-break">{user?.name } </a>
                            <div className="navi mt-2">
                                <a className="navi-item">
                                    <span className="navi-link p-0 pb-2 align-items-start">
                                        <span className="navi-icon mr-1">
                                            <span className="svg-icon svg-icon-lg svg-icon-primary">

                                                <svg xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                                                    <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                                                        <rect x="0" y="0" width="24" height="24"></rect>
                                                        <path d="M21,12.0829584 C20.6747915,12.0283988 20.3407122,12 20,12 C16.6862915,12 14,14.6862915 14,18 C14,18.3407122 14.0283988,18.6747915 14.0829584,19 L5,19 C3.8954305,19 3,18.1045695 3,17 L3,8 C3,6.8954305 3.8954305,6 5,6 L19,6 C20.1045695,6 21,6.8954305 21,8 L21,12.0829584 Z M18.1444251,7.83964668 L12,11.1481833 L5.85557487,7.83964668 C5.4908718,7.6432681 5.03602525,7.77972206 4.83964668,8.14442513 C4.6432681,8.5091282 4.77972206,8.96397475 5.14442513,9.16035332 L11.6444251,12.6603533 C11.8664074,12.7798822 12.1335926,12.7798822 12.3555749,12.6603533 L18.8555749,9.16035332 C19.2202779,8.96397475 19.3567319,8.5091282 19.1603533,8.14442513 C18.9639747,7.77972206 18.5091282,7.6432681 18.1444251,7.83964668 Z" fill="#000000"></path>
                                                        <circle fill="#000000" opacity="0.3" cx="19.5" cy="17.5" r="2.5"></circle>
                                                    </g>
                                                </svg>

                                            </span>
                                        </span>
                                        <span className="navi-text text-muted text-hover-primary user_email_Id" onClick={()=>navigator.clipboard.writeText(user?.emailId)}>{user?.emailId}</span>
                                    </span>
                                </a>
                                <a onClick={() => this.logOutAccount()} className="whitelabelanchor btn btn-sm btn-secondary font-weight-bolder py-2 px-5">Sign Out</a> </div>
                        </div>
                    </div>


                 








             


                    <div className="separator separator-dashed my-7"></div>

                </div>

            </div>
        )
    }
}
